import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../styles.css';

function Register({ countries }) {
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [birthYear, setBirthYear] = useState('');
  const [countryCode, setCountryCode] = useState('');
  const [password, setPassword] = useState('');
  const [personId, setPersonId] = useState(null);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleRegister = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch('http://localhost:5014/api/users/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          first_name: firstName,
          last_name: lastName,
          birth_year: birthYear,
          country_code: countryCode,
          password,
        }),
      });

      const data = await response.json();
      if (response.ok) {
        setPersonId(data.person_id);
        alert(`Registration successful!`);
        navigate('/');
      } else {
        setError(data.error || 'Failed to register');
      }
    } catch (err) {
      setError('Failed to connect to the server');
    }
  };

  return (
    <div className="login-container">
      <div className="login-card">
        <h2>Create an Account</h2>
        <form onSubmit={handleRegister}>
          <input
            type="text"
            placeholder="First Name"
            value={firstName}
            onChange={(e) => setFirstName(e.target.value)}
            className="login-input"
          />
          <input
            type="text"
            placeholder="Last Name"
            value={lastName}
            onChange={(e) => setLastName(e.target.value)}
            className="login-input"
          />
          <input
            type="number"
            placeholder="Birth Year"
            value={birthYear}
            onChange={(e) => setBirthYear(e.target.value)}
            className="login-input"
          />
          <select
            value={countryCode}
            onChange={(e) => setCountryCode(e.target.value)}
            className="login-input"
          >
            <option value="">Select Country</option>
            {Object.entries(countries).map(([name, code]) => (
              <option key={code} value={code}>
                {name} ({code})
              </option>
            ))}
          </select>
          <input
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="login-input"
          />
          {error && <p className="error-text">{error}</p>}
          {personId && (
            <p className="success-text">Your Person ID: {personId}</p>
          )}
          <button type="submit" className="login-button">
            Register
          </button>
        </form>
        <p>
          Already have an account?{' '}
          <span className="link-text" onClick={() => navigate('/')}>
            Login
          </span>
        </p>
      </div>
    </div>
  );
}

export default Register;
