import React, { useState, useEffect } from 'react';
import axios from '../../services/api';
import { ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import '../../styles.css';

function DisorderCorrelation({ disorders }) {
  const [disorder1, setDisorder1] = useState("eating");
  const [disorder2, setDisorder2] = useState("anxiety");
  const [data, setData] = useState([]);

  const fetchData = async () => {
    try {
      const response = await axios.get(`/disorders/correlation/${disorder1}/${disorder2}`);
      setData(response.data);
    } catch (error) {
      console.error('Error fetching disorder correlation data:', error);
    }
  };

  useEffect(() => {
    if (disorder1 && disorder2) {
      fetchData();
    }
  }, [disorder1, disorder2]);

  return (
    <div className="query-section">
      <h2>Disorder Correlation Globally</h2>
      <div style={{ marginBottom: '20px', display: 'flex', gap: '10px', justifyContent: 'center' }}>
        {/* Dropdown for Disorder 1 */}
        <select
          value={disorder1}
          onChange={(e) => setDisorder1(e.target.value)}
          className="login-input"
          style={{ padding: '10px', borderRadius: '5px', border: '1px solid #ccc', width: '200px' }}
        >
          <option value="">Select Disorder 1</option>
          {disorders.map((disorder) => (
            <option key={disorder.disorder_type} value={disorder.disorder_type}>
              {disorder.disorder_type}
            </option>
          ))}
        </select>

        {/* Dropdown for Disorder 2 */}
        <select
          value={disorder2}
          onChange={(e) => setDisorder2(e.target.value)}
          className="login-input"
          style={{ padding: '10px', borderRadius: '5px', border: '1px solid #ccc', width: '200px' }}
        >
          <option value="">Select Disorder 2</option>
          {disorders.map((disorder) => (
            <option key={disorder.disorder_type} value={disorder.disorder_type}>
              {disorder.disorder_type}
            </option>
          ))}
        </select>
      </div>
      {/*Display graph to get relations */}
      {data.length > 0 ? (
        <div style={{ width: '100%', height: 400 }}>
          <ResponsiveContainer>
            <ScatterChart>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="disorder1_prevalence" name="Disorder 1 Prevalence" />
              <YAxis dataKey="disorder2_prevalence" name="Disorder 2 Prevalence" />
              <Tooltip />
              <Scatter
                name="Countries"
                data={data}
                fill="#4e54c8"
              />
            </ScatterChart>
          </ResponsiveContainer>
        </div>
      ) : (
        <p style={{ textAlign: 'center' }}>No data available. Please select valid disorders.</p>
      )}
    </div>
  );
}

export default DisorderCorrelation;
