import React, { useState, useEffect } from 'react';
import axios from '../../services/api';
import regression from 'regression';
import {
  Chart as ChartJS,
  LineElement,
  PointElement,
  LinearScale,
  Title,
  Tooltip,
  Legend,
  CategoryScale,
} from 'chart.js';
import { Line } from 'react-chartjs-2';

ChartJS.register(LineElement, PointElement, LinearScale, Title, Tooltip, Legend, CategoryScale);

function TrendLine({ countries }) {
  const [countryCode, setCountryCode] = useState(localStorage.getItem('birthCountry') || '');
  const [data, setData] = useState([]);
  const [prediction, setPrediction] = useState(null);
  const [chartData, setChartData] = useState(null);
  const [error, setError] = useState('');

  const fetchData = async () => {
    try {
      const response = await axios.get(`/disorders/trend-line/${countryCode}`);
      const trendData = response.data.map((item) => [
        item.year,
        item.overall_prevalence,
      ]);
      setData(response.data);

      // Calculate trend line and predict the next year's prevalence
      const result = regression.linear(trendData);
      const gradient = result.equation[0];
      const yIntercept = result.equation[1];

      // Predict the next year's prevalence
      const nextYear = trendData[trendData.length - 1][0] + 1;
      const nextYearPrevalence = gradient * nextYear + yIntercept;
      setPrediction({ year: nextYear, prevalence: nextYearPrevalence });

      // Prepare chart data
      const labels = trendData.map((item) => item[0]);
      const values = trendData.map((item) => item[1]);

      setChartData({
        labels: [...labels, nextYear],
        datasets: [
          {
            label: 'Prevalence Trend',
            data: values,
            borderColor: 'blue',
            fill: false,
            tension: 0.1,
          },
          {
            label: 'Prediction',
            data: [...values, nextYearPrevalence],
            borderColor: 'red',
            borderDash: [5, 5],
            pointBackgroundColor: 'red',
            pointBorderColor: 'red',
            tension: 0.1,
          },
        ],
      });
      setError('');
    } catch (error) {
      console.error('Error fetching trend line:', error);
      setError('Failed to fetch trend data. Please try again.');
    }
  };

  useEffect(() => {
    if (countryCode) fetchData();
  }, [countryCode]);

  return (
    <div className="query-section">
      <h2>Trend Line</h2>
      <select
        value={countryCode}
        onChange={(e) => setCountryCode(e.target.value)}
        className="login-input"
      >
        <option value="">Select Country</option>
        {Object.entries(countries).map(([name, code]) => (
          <option key={code} value={code}>
            {name} ({code})
          </option>
        ))}
      </select>
      <div>
        {error && <p className="error-text">{error}</p>}
        {data.length > 0 && chartData ? (
          <>
            <div style={{ width: '80%', margin: 'auto' }}>
              <Line
                data={chartData}
                options={{
                  responsive: true,
                  plugins: {
                    legend: {
                      labels: {
                        filter: (item) => item.text !== '', 
                      },
                    },
                  },
                  scales: {
                    x: { title: { display: true, text: 'Year' } },
                    y: { title: { display: true, text: 'Prevalence' } },
                  },
                }}
              />
            </div>
            {prediction && (
              <p style={{ textAlign: 'center', marginTop: '10px' }}>
                Predicted Prevalence for {prediction.year}:{' '}
                <strong>{prediction.prevalence.toFixed(2)}</strong>
              </p>
            )}
          </>
        ) : (
          !error && <p>No data available</p>
        )}
      </div>
    </div>
  );
}

export default TrendLine;
