import React, { useState, useEffect } from 'react';
import axios from '../../services/api';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

function DepressionGenderComparison({ countries }) {
  const [countryCode, setCountryCode] = useState(localStorage.getItem('birthCountry') || '');
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);

  const fetchData = async () => {
    if (!countryCode) return;
    setLoading(true);

    try {
      const response = await axios.get(`/disorders/depression-comparison/${countryCode}`);
      const rawData = response.data;

      //The two groups that the data specifies gender
      const groupedData = [
        {
          group: 'Depressive',
          male: rawData.find((item) => item.disorder_type === 'depressive' && item.gender === 'male')?.prevalence || 0,
          female: rawData.find((item) => item.disorder_type === 'depressive' && item.gender === 'female')?.prevalence || 0,
          both: rawData.find((item) => item.disorder_type === 'depressive' && item.gender === 'both')?.prevalence || 0,
        },
        {
          group: 'Overall',
          male: rawData.find((item) => item.disorder_type === 'overall' && item.gender === 'male')?.prevalence || 0,
          female: rawData.find((item) => item.disorder_type === 'overall' && item.gender === 'female')?.prevalence || 0,
        },
      ];

      setData(groupedData);
    } catch (error) {
      console.error('Error fetching gender comparison data:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, [countryCode]);

  return (
    <div className="query-section">
      <h2>Gender Compare</h2>

      {/* Country Dropdown */}
      <select
        value={countryCode}
        onChange={(e) => setCountryCode(e.target.value)}
        className="login-input"
      >
        <option value="">Select Country</option>
        {Object.entries(countries).map(([name, code]) => (
          <option key={code} value={code}>
            {name} ({code})
          </option>
        ))}
      </select>

      {/* Loading State */}
      {loading ? (
        <p style={{ textAlign: 'center', marginTop: '20px' }}>Loading data...</p>
      ) : (
        <div style={{ width: '100%', height: 400, marginTop: '20px' }}>
          {data.length > 0 ? (
            <ResponsiveContainer>
              <BarChart data={data} margin={{ top: 20, right: 30, left: 20, bottom: 5 }}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="group" />
                <YAxis label={{ value: 'Prevalence', angle: -90, position: 'insideLeft' }} />
                <Tooltip />
                <Legend />
                <Bar dataKey="male" fill="#4e54c8" name="Male" />
                <Bar dataKey="female" fill="#8f94fb" name="Female" />
                <Bar dataKey="both" fill="#66c2a5" name="Both (Depressive only)" />
              </BarChart>
            </ResponsiveContainer>
          ) : (
            <p style={{ textAlign: 'center', marginTop: '20px' }}>No data available. Please select a country.</p>
          )}
        </div>
      )}
    </div>
  );
}

export default DepressionGenderComparison;
