import React, { useState, useEffect } from 'react';
import { BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer, CartesianGrid } from 'recharts';
import Dropdown from '../Dropdown';
import api from '../../services/api';

function TopDisorders({ countries }) {
  const [selectedCountry, setSelectedCountry] = useState(localStorage.getItem('birthCountry') || '');
  const [chartData, setChartData] = useState([]);
  const [loading, setLoading] = useState(false);

  const fetchDisordersData = async (countryCode) => {
    setLoading(true);

    try {
      const response = await api.get(`/disorders/top-disorders/${countryCode}`);
      setChartData(response.data);
    } catch (error) {
      console.error('Error fetching disorders data:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (selectedCountry) {
      fetchDisordersData(selectedCountry);
    }
  }, [selectedCountry]);

  const dropdownOptions = Object.entries(countries).map(([name, code]) => ({
    label: `${name} (${code})`,
    value: code,
  }));

  return (
    <div style={{ width: '80%', margin: '20px auto', backgroundColor: '#fff', padding: '20px', borderRadius: '8px' }}>
      <h2 style={{ textAlign: 'center' }}>Top Disorders by Country</h2>

      {/* Dropdown for Country Selection */}
      <Dropdown
        options={dropdownOptions}
        value={selectedCountry} 
        onChange={(e) => setSelectedCountry(e.target.value)}
        placeholder= {selectedCountry ? selectedCountry : 'Select Country'}
      />

      {/* Display Selected Country */}
      <h3 style={{ textAlign: 'center', margin: '20px 0' }}>
        {selectedCountry ? `Selected Country: ${selectedCountry}` : 'Please select a country'}
      </h3>

      {/* Loading State */}
      {loading ? (
        <p style={{ textAlign: 'center' }}>Loading data...</p>
      ) : (
        <div style={{ width: '100%', height: '300px', marginTop: '20px' }}>
          {/* Bar Chart for Visualization */}
          {chartData.length > 0 ? (
            <ResponsiveContainer>
              <BarChart data={chartData} margin={{ top: 20, right: 30, left: 20, bottom: 5 }}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="disorder_type" label={{ value: 'Disorder Type', position: 'insideBottom', offset: -5 }} />
                <YAxis label={{ value: 'Prevalence', angle: -90, position: 'insideLeft' }} />
                <Tooltip />
                <Bar dataKey="prevalence" fill="#4e54c8" barSize={30} />
              </BarChart>
            </ResponsiveContainer>
          ) : (
            <p style={{ textAlign: 'center' }}>No data available. Please select a country.</p>
          )}
        </div>
      )}
    </div>
  );
}

export default TopDisorders;
