import React, { useState, useEffect } from 'react';
import axios from '../../services/api';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

function DalyGrowth({ countries, disorders }) {
  const [countryCode, setCountryCode] = useState(localStorage.getItem('birthCountry') || '');
  const [disorderId, setDisorderId] = useState(2);
  const [data, setData] = useState([]);

  const fetchData = async () => {
    try {
      const response = await axios.get(`/disorders/daly-growth/${countryCode}/${disorderId}`);
      setData(response.data);
    } catch (error) {
      console.error('Error fetching DALY growth:', error);
    }
  };

  useEffect(() => {
    if (countryCode && disorderId) fetchData();
  }, [countryCode, disorderId]);

  return (
    <div className="query-section">
      <h2>DALY Growth</h2>

      {/* Country Dropdown */}
      <select
        value={countryCode}
        onChange={(e) => setCountryCode(e.target.value)}
        className="login-input"
      >
        <option value="">Select Country</option>
        {Object.entries(countries).map(([name, code]) => (
          <option key={code} value={code}>
            {name} ({code})
          </option>
        ))}
      </select>

      {/* Disorder Dropdown */}
      <select
        value={disorderId}
        onChange={(e) => setDisorderId(e.target.value)}
        className="login-input"
      >
        <option value="">Select Disorder</option>
        {disorders.map((disorder) => (
          <option key={disorder.disorder_id} value={disorder.disorder_id}>
            {disorder.disorder_type}
          </option>
        ))}
      </select>

      {/* Line Chart Visualization */}
      <div style={{ width: '100%', height: 400, marginTop: '20px' }}>
        {data.length > 0 ? (
          <ResponsiveContainer>
            <LineChart data={data} margin={{ top: 20, right: 30, left: 20, bottom: 5 }}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="year" label={{ value: 'Year', position: 'insideBottom', offset: -5 }} />
              <YAxis label={{ value: 'DALY', angle: -90, position: 'insideLeft' }} />
              <Tooltip />
              <Line type="monotone" dataKey="daly" stroke="#4e54c8" strokeWidth={2} dot={{ r: 4 }} />
            </LineChart>
          </ResponsiveContainer>
        ) : (
          <p style={{ textAlign: 'center', marginTop: '20px' }}>No data available</p>
        )}
      </div>
    </div>
  );
}

export default DalyGrowth;
