import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom'; 
import TopDisorders from './queries/TopDisorders';
import DalyGrowth from './queries/DalyGrowth';
import GenderGap from './queries/GenderGap';
import TrendLine from './queries/TrendLine';
import DepressionGenderComparison from './queries/DepressionGenderComparison';
import DalyHeatmap from './queries/DalyHeatmap';
import TopDalyImprovement from './queries/TopDalyImprovement';
import DisorderCorrelation from './queries/DisorderCorrelation';
import TopCountriesByDisorder from './queries/TopCountriesByDisorder';
import DisorderPrevalenceByContinent from './queries/DisorderPrevalenceByContinent';
import MinDalyDifferenceAnxiety from './queries/MinDalyDifferenceAnxiety';

import '../styles.css';

function MainScreen({ countries, disorders }) {
  const navigate = useNavigate();

  useEffect(() => {
    const tempyear = localStorage.getItem('birthYear');
    if (!tempyear) {
      // Redirect to login page if no user is found
      navigate('/');
    }
  }, [navigate]);

  const handleSignOut = () => {
    localStorage.clear();
    // Redirect to the login page
    navigate('/');
  };

  return (
    <div className="main-container">
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '20px' }}>
        <h1>Main Visualization Screen</h1>
        {/* Sign Out Button */}
        <button 
          onClick={handleSignOut} 
          className="sign-out-button" 
          style={{
            padding: '10px 20px',
            backgroundColor: '#4e54c8',
            color: '#fff',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer',
            fontWeight: 'bold',
          }}
        >
          Sign Out
        </button>
      </div>

      <div className="query-grid">
        <div className="query-item">
          <TopDisorders countries={countries} />
        </div>
        <div className="query-item">
          <DalyGrowth countries={countries} disorders={disorders} />
        </div>
        <div className="query-item">
          <GenderGap />
        </div>
        <div className="query-item">
          <DepressionGenderComparison countries={countries} />
        </div>
        <div className="query-item">
          <TopDalyImprovement />
        </div>
        <div className="query-item">
          <DisorderCorrelation disorders={disorders} />
        </div>
        <div className="query-item">
          <TopCountriesByDisorder disorders={disorders} />
        </div>
        <div className="query-item">
          <MinDalyDifferenceAnxiety />
        </div>
      </div>

      {/* Standalone items */}
      <div className="standalone-item">
        <DalyHeatmap countries={countries} disorders={disorders} />
      </div>
      <div className="standalone-item">
        <DisorderPrevalenceByContinent />
      </div>
      <div className="query-item">
        <TrendLine countries={countries} />
      </div>
    </div>
  );
}

export default MainScreen;
