import React, { useState, useEffect } from 'react';
import axios from '../../services/api';
import { Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

function GenderGap() {
  const [data, setData] = useState([]);

  const fetchData = async () => {
    try {
      const response = await axios.get('/disorders/gender-gap');
      setData(response.data);
    } catch (error) {
      console.error('Error fetching gender gap data:', error);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  {/*Map by top conuntries with gap */}
  const chartData = {
    labels: data.map((item) => item.country_name),
    datasets: [
      {
        label: 'Gender Gap (Depressive)',
        data: data.map((item) => item.gender_gap),
        backgroundColor: 'rgba(75, 192, 192, 0.6)',
        borderColor: 'rgba(75, 192, 192, 1)',
        borderWidth: 1,
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        display: true,
        position: 'top',
      },
      title: {
        display: true,
        text: 'Top 5 Countries with Largest Gender Gap in Depression',
      },
    },
    scales: {
      x: {
        title: {
          display: true,
          text: 'Country',
        },
      },
      y: {
        title: {
          display: true,
          text: 'Gender Gap',
        },
        beginAtZero: true,
      },
    },
  };

  return (
    <div className="query-section">
      <h2>Top 5 Countries with Gender Gap in Depression</h2>
      {data.length > 0 ? (
        <div style={{ width: '80%', margin: 'auto' }}>
          <Bar data={chartData} options={options} />
        </div>
      ) : (
        <p>No data available.</p>
      )}
    </div>
  );
}

export default GenderGap;
