import React, { useState, useEffect } from 'react';
import axios from '../../services/api';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import '../../styles.css';

function TopCountriesByDisorder({ disorders }) {
  const [disorderId, setDisorderId] = useState(1);
  const [data, setData] = useState([]);

  const fetchData = async () => {
    try {
      const response = await axios.get(`/disorders/top-countries/${disorderId}`);
      setData(response.data);
    } catch (error) {
      console.error('Error fetching top countries by disorder:', error);
    }
  };

  useEffect(() => {
    if (disorderId) {
      fetchData();
    }
  }, [disorderId]);

  return (
    <div className="query-section">
      <h2>Top Ten Countries by Disorder</h2>
      <div style={{ marginBottom: '20px' }}>
        {/* Disorder Dropdown */}
        <select
          value={disorderId}
          onChange={(e) => setDisorderId(e.target.value)}
          className="login-input"
        >
          <option value="">Select Disorder</option>
          {disorders.map((disorder) => (
            <option key={disorder.disorder_id} value={disorder.disorder_id}>
              {disorder.disorder_type}
            </option>
          ))}
        </select>
      </div>
      {data.length > 0 ? (
        <div style={{ width: '100%', height: 400 }}>
          <ResponsiveContainer>
            <BarChart
              data={data}
              layout="vertical"
              margin={{ top: 20, right: 30, left: 100, bottom: 5 }}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis type="number" dataKey="prevalence" />
              <YAxis type="category" dataKey="country_name" width={150} />
              <Tooltip />
              <Bar dataKey="prevalence" fill="#4e54c8" />
            </BarChart>
          </ResponsiveContainer>
        </div>
      ) : (
        <p>No data available. Please select a valid disorder.</p>
      )}
    </div>
  );
}

export default TopCountriesByDisorder;
