import React, { useState, useEffect } from 'react';
import axios from '../../services/api';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import '../../styles.css';

function DisorderPrevalenceByContinent() {
  const [data, setData] = useState([]);

  const fetchData = async () => {
    try {
      const response = await axios.get('/disorders/continent-prevalence');
      setData(formatData(response.data));
    } catch (error) {
      console.error('Error fetching disorder prevalence by continent:', error);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const formatData = (rows) => {
    const formatted = {};
    rows.forEach((row) => {
      const { continent, disorder_type, prevalence } = row;
      if (!formatted[continent]) {
        formatted[continent] = { continent };
      }
      formatted[continent][disorder_type] = prevalence;
    });
    return Object.values(formatted);
  };

  return (
    <div className="query-section">
      <h2>Disorder Prevalence by Continent</h2>
      {data.length > 0 ? (
        <div style={{ width: '100%', height: 500 }}>
          <ResponsiveContainer>
            <BarChart
              data={data}
              layout="vertical"
              margin={{ top: 20, right: 30, left: 100, bottom: 5 }}
              barCategoryGap={10}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis type="number" />
              <YAxis type="category" dataKey="continent" width={150} />
              <Tooltip />
              <Legend />
              {/*Map by continent */}
              {Object.keys(data[0])
                .filter((key) => key !== 'continent')
                .map((key, index) => (
                  <Bar
                    key={key}
                    dataKey={key}
                    fill={getBarColor(index)}
                    barSize={12}
                  />
                ))}
            </BarChart>
          </ResponsiveContainer>
        </div>
      ) : (
        <p>No data available.</p>
      )}
    </div>
  );
}

const getBarColor = (index) => {
  const colors = ['#4e54c8', '#8f94fb', '#ff7f50', '#87ceeb', '#32cd32', '#ff6347', '#ffa500', '#9370db', '#00ced1'];
  return colors[index % colors.length];
};

export default DisorderPrevalenceByContinent;
