import React, { useState, useEffect, useRef } from 'react';
import axios from '../../services/api';
import '../../styles.css';

function DalyHeatmap({ disorders }) {
  const [year, setYear] = useState(() => {
    const savedYear = localStorage.getItem('birthYear');
    let fixedYear = savedYear >= 1990 ? savedYear : 1990;
    fixedYear = savedYear <= 2019 ? fixedYear : 2019;
    return fixedYear;
  });
  const [disorderId, setDisorderId] = useState(1);
  const [data, setData] = useState([]);
  const mapContainerRef = useRef(null);

  const fetchData = async () => {
    if (!year || !disorderId) return;

    try {
      const response = await axios.get(`/disorders/daly-year/${year}/${disorderId}`);
      setData(response.data);
    } catch (error) {
      console.error('Error fetching DALY data:', error);
    }
  };

  useEffect(() => {
    fetchData();
  }, [year, disorderId]);

  useEffect(() => {
    if (data.length > -1) {
      applyColorsToSVG();
    }
  }, [data]);

  //Colors from green all the way ti red for level of severity
  const getColor = (daly) => {
    if (daly < 0.25) return '#2ecc71';
    if (daly < 0.5) return '#27ae60';
    if (daly < 0.75) return '#a6d96a';
    if (daly < 1) return '#f1c40f';
    if (daly < 1.25) return '#f7dc6f';
    if (daly < 1.5) return '#fdae61';
    if (daly < 1.75) return '#e67e22';
    if (daly < 2) return '#d35400';
    if (daly < 2.25) return '#f46d43';
    if (daly < 2.5) return '#e74c3c';
    if (daly < 2.75) return '#d73027';
    if (daly < 3) return '#b71540';
    if (daly < 3.25) return '#a50026';
    if (daly < 3.5) return '#7f0000';
    if (daly < 3.75) return '#660000';
    if (daly <= 4) return '#4d0000';
    // Gray for high values
    return '#808080';
  };

  const applyColorsToSVG = () => {
    const svgObject = document.getElementById('svg-map');
    if (!svgObject) return;

    const applyColors = () => {
      const svgDoc = svgObject.contentDocument;
      if (svgDoc) {
        const allPaths = svgDoc.querySelectorAll('path');
        allPaths.forEach((path) => {
          // Country appears in svg as one item called name or as group of items 
          //(each item is a piece of land) called class
          const countryName = path.getAttribute('name') || path.getAttribute('class');
          if (countryName) {
            const countryData = data.find(
              (item) =>
                item.country_name === countryName || item.country_name === countryName.replace(/-/g, ' ')
            );

            if (countryData) {
              path.style.fill = getColor(countryData.daly);
            } else {
              // Default for no match
              path.style.fill = '#d3d3d3';
            }
          }
        });
      }
    };

    if (svgObject.contentDocument) {
      applyColors();
    } else {
      svgObject.addEventListener('load', applyColors, { once: true });
    }
  };

  const handleWheel = (e) => {
    e.preventDefault();
    const scaleAmount = e.deltaY > 0 ? 0.9 : 1.1;
    const mapContainer = mapContainerRef.current;
    if (mapContainer) {
      const currentScale = parseFloat(mapContainer.style.transform.replace(/scale\((.*)\)/, '$1')) || 1;
      const newScale = Math.min(Math.max(currentScale * scaleAmount, 0.5), 5);
      mapContainer.style.transform = `scale(${newScale})`;
    }
  };

  const handleMouseDrag = (e) => {
    if (!e.ctrlKey) return;
    e.preventDefault();
    const mapContainer = mapContainerRef.current;
    if (mapContainer) {
      mapContainer.scrollLeft -= e.movementX;
      mapContainer.scrollTop -= e.movementY;
    }
  };

  return (
    <div className="query-section" style={{ padding: '20px', backgroundColor: '#f8f9fa' }}>
      <h2 style={{ textAlign: 'center', marginBottom: '20px' }}>DALY Heatmap by Year</h2>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          gap: '10px',
          marginBottom: '20px',
        }}
      >
        {/*Input of year and disorder */}
        <input
          type="number"
          placeholder="Enter Year"
          value={year}
          onChange={(e) => setYear(e.target.value)}
          className="login-input"
          min="1990"
          max="2019"
          style={{
            padding: '10px',
            borderRadius: '5px',
            border: '1px solid #ccc',
            width: '150px',
            textAlign: 'center',
          }}
        />
        <select
          value={disorderId}
          onChange={(e) => setDisorderId(e.target.value)}
          className="login-input"
          style={{
            padding: '10px',
            borderRadius: '5px',
            border: '1px solid #ccc',
            width: '200px',
            textAlign: 'center',
          }}
        >
          <option value="">Select Disorder</option>
          {disorders.map((disorder) => (
            <option key={disorder.disorder_id} value={disorder.disorder_id}>
              {disorder.disorder_type}
            </option>
          ))}
        </select>
      </div>
      {/* Display map */}
      <div
        className="map-container"
        ref={mapContainerRef}
        onWheel={handleWheel}
        onMouseMove={handleMouseDrag}
        style={{
          overflow: 'auto',
          border: '1px solid #ccc',
          borderRadius: '10px',
          width: '100%',
          height: '600px',
          margin: '0 auto',
          cursor: 'grab',
        }}
      >
        <object
          id="svg-map"
          data="/world.svg"
          type="image/svg+xml"
          style={{
            width: '2000px',
            height: '1200px',
          }}
        >
          Your browser does not support SVG.
        </object>
      </div>
    </div>
  );
}

export default DalyHeatmap;
