import React, { useState, useEffect } from 'react';
import axios from '../../services/api';

function MinDalyDifferenceAnxiety() {
  const [year, setYear] = useState(() => {
    const savedYear = localStorage.getItem('birthYear');
    let fixedYear = savedYear >= 1990 ? savedYear : 1990;
    fixedYear = savedYear <= 2019 ? savedYear : 2019;
    return fixedYear;
  });
  const [data, setData] = useState(null);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchData = async () => {
      if (!year) {
        setData(null);
        setError('');
        return;
      }
      try {
        const response = await axios.get(`/disorders/min-daly-difference/${year}`);
        console.log('Min DALY difference:', response.data);
        setData(response.data);
        setError('');
      } catch (error) {
        console.error('Error fetching minimum DALY difference:', error);
        setData(null);
        setError('Failed to fetch data. Please try again.');
      }
    };

    fetchData();
  }, [year]);

  return (
    <div className="query-section">
      <h2>Minimum DALY Difference (Anxiety)</h2>
      {/* Year Dropdown */}
      <select
        value={year}
        onChange={(e) => setYear(e.target.value)}
        className="login-input"
        style={{ marginBottom: '20px', padding: '10px', borderRadius: '5px', border: '1px solid #ccc' }}
      >
        {[...Array(30)].map((_, i) => {
          const optionYear = 1990 + i;
          return (
            <option key={optionYear} value={optionYear}>
              {optionYear}
            </option>
          );
        })}
      </select>
      {error && <p className="error-text">{error}</p>}
      {data ? (
        <div className="result-box">
          <p><strong>Year:</strong> {year}</p>
          <p><strong>Country 1:</strong> {data.country1}</p>
          <p><strong>Country 2:</strong> {data.country2}</p>
          <p><strong>DALY Difference:</strong> {parseFloat(data.daly_difference).toFixed(6)}</p>
          <p><strong>Prevalence Difference:</strong> {parseFloat(data.prevalence_difference).toFixed(6)}</p>
        </div>
      ) : (
        <p>No data available</p>
      )}
    </div>
  );
}

export default MinDalyDifferenceAnxiety;
