import React, { useState, useEffect } from 'react';
import axios from '../../services/api';
import { BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';
import '../../styles.css';

function TopDalyImprovement() {
  const [data, setData] = useState([]);

  const fetchData = async () => {
    try {
      const response = await axios.get('/disorders/top-daly-improvement');
      setData(response.data);
    } catch (error) {
      console.error('Error fetching top DALY improvement data:', error);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  return (
    <div className="query-section">
      <h2>Top 5 Countries with Most DALY Reduction</h2>
      {data.length > 0 ? (
        <div style={{ width: '100%', height: 400 }}>
          <ResponsiveContainer>
            <BarChart data={data}>
              <XAxis dataKey="country_name" />
              <YAxis />
              <Tooltip />
              <Bar dataKey="daly_reduction" fill="#4e54c8" />
            </BarChart>
          </ResponsiveContainer>
        </div>
      ) : (
        <p>No data available. Please check the API response.</p>
      )}
    </div>
  );
}

export default TopDalyImprovement;
