import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import MainScreen from './components/MainScreen';
import Register from './components/Register';
import Login from './components/Login';

function App() {
  const [countries, setCountries] = useState({});
  const [disorders, setDisorders] = useState([]);

  useEffect(() => {
    const fetchCountries = async () => {
      try {
        const response = await fetch('http://localhost:5014/api/countries');
        const data = await response.json();
        setCountries(data);
      } catch (error) {
        console.error('Failed to fetch countries:', error);
      }
    };

    const fetchDisorders = async () => {
      try {
        const response = await fetch('http://localhost:5014/api/disorders/all');
        const data = await response.json();
        setDisorders(data);
      } catch (error) {
        console.error('Failed to fetch disorders:', error);
      }
    };

    fetchCountries();
    fetchDisorders();
  }, []);

  return (
    <Router>
      <Routes>
        <Route path="/" element={<Login />} />
        <Route path="/register" element={<Register countries={countries} />} />
        <Route path="/main" element={<MainScreen countries={countries} disorders={disorders} />} />
      </Routes>
    </Router>
  );
}

export default App;
